import React, { Suspense } from 'react';
import { LoadingSpinner } from './LoadingSpinner';
import Banner from '../components/Banner';
import { Link } from 'gatsby';

export const MoreInformation = () => (
  <Banner
    backgroundImage='https://res.cloudinary.com/dtweazqf2/image/upload/q_auto,f_auto/v1694872808/smackysmacky_nygpmn.jpg'
    backgroundPosition='center'>
    <div
      aria-label='more information'
      className={`flex flex-col bg-black/50 w-full px-4 pt-4 pb-10 md:pb-28 items-start rounded-lg text-white`}
    >
      <p className='text-xs sm:text-sm md:text-md w-full justify-between flex flex-nowrap'>
        For information on class schedule and pricing, please visit our
        <Link
          to='/information'
          className='text-xs sm:text-sm underline hover:no-underline'
        >
          INFORMATION
        </Link>
        page.
      </p>
    </div>
  </Banner>
)

const LazyMoreInformation = () => (
  <Suspense fallback={<LoadingSpinner />}>
    <MoreInformation />
  </Suspense>
)

export default LazyMoreInformation
